<template>
    <div>
        <h1>{{msg}}</h1>
        <button v-on:click="clickMe" v-bind:title="myTitle+'hello'">{{clic}}</button>
        <h2>{{msgFatherSay}}</h2>
    </div>

</template>

<script>
    export default {
        name: "componentA",
        data () {
            return {
                msg: 'this is componentA',
                clic: '点击',
                myTitle: '按钮'
            }
        },
        props: ['msgFatherSay'],
        methods: {
            clickMe: function () {
                console.log(this.msgFatherSay)
            }
        }
    }
</script>

<style scoped>

</style>


