<template>
    <div class="router3">
        <h1>跑马灯效果</h1>
        <input type="button" v-on:click="go()" value="浪起来!">
        <input type="button" v-on:click="stop" value="稳住!">
        <h2>{{msg}}</h2>

        <div class="some">
            <h3>事件修饰符</h3>
            <h4>.stop:阻止事件冒泡</h4>
            <h4>.prevent:阻止默认行为</h4>
            <h4>.capture:实现捕获触发事件的机制，即反过来冒泡</h4>
            <h4>.self:实现只有点击当前元素，才会触发事件处理函数</h4>
            <h4>.once:只触发一次事件处理函数</h4>
        </div>


    </div>

</template>

<script>
    export default {
        name: "router3",
        data() {
            return {
                msg: '猥琐发育，别浪~~~!!!',
                setIn: null
            }
        },
        methods: {
            go: function () {


                if(this.setIn != null) return
                this.setIn = setInterval( () => {
                    var start = this.msg.substring(0,1)
                    var end = this.msg.substring(1,this.msg.length)
                    this.msg = end + start

                },250)

            },
            stop: function () {
                clearInterval(this.setIn)
                this.setIn = null
            }
        }
    }
</script>

<style scoped>
    .some{
        width: 500px;
        margin: 0 auto;
        text-align: left;
        background-color: beige;
    }
</style>


