<template>
    <div>
        <h1>{{router2}}</h1>
        <input type="text" v-model="newItem" v-on:keyup.enter="add(items)">
        <ul>
            <li v-for="item in items" v-bind:class="{finished:item.isFinished}" v-on:click="fini(item)">{{item.label}}</li>
        </ul>
    </div>
</template>

<script>
    import Store from '../store'
    export default {
        name: "router2",
        comments: {

        },
        data() {
            return {
                router2: 'this is a todolist',
                items:Store.fetch(),
                newItem: ''
            }
        },
        methods : {
            add: function (items) {
                items.push({
                    label: this.newItem,
                    isFinished: false
                })
                this.newItem = ''
            },
            fini: function (item) {
                item.isFinished = !item.isFinished
            }
        },
        watch : {
            items : {
                handler : function (items) {
                    Store.save(items)
                },
                deep: true
            }
        }
    }
</script>

<style scoped>
    .finished{
        text-decoration: line-through;
    }
    li{
        list-style: none;
    }
</style>


