<template>
    <div>
        <h3>简易计算器</h3>
        <input type="text" v-model="n1">
        <select v-model="opt">
            <option value="+">+</option>
            <option value="-">-</option>
            <option value="*">*</option>
            <option value="/">/</option>
        </select>
        <input type="text" v-model="n2">
        <input type="button" value="=" v-on:click="res">
        <input type="text" v-model="result">
    </div>
</template>

<script>
    export default {
        name: "Calculation",
        data() {
            return {
                n1: 0,
                n2: 0,
                result: 0,
                opt: '+'
            }
        },
        methods: {
            res() {
                switch (this.opt) {
                    case '+' :  this.result = parseInt(this.n1) + parseInt(this.n2)
                        break
                    case '-' :  this.result = parseInt(this.n1) - parseInt(this.n2)
                        break
                    case '*' :  this.result = parseInt(this.n1) * parseInt(this.n2)
                        break
                    case '/' :  this.result = parseInt(this.n1) / parseInt(this.n2)
                        break
                }
            }
        }
    }
</script>

<style scoped>

</style>


