<template>
    <div class="router">
        <h1>{{router1}}</h1>
        <input type="text" v-model="newVal">
        <p>: {{newVal}}</p>
        <component-a v-bind:msg-father-say="newVal"></component-a>
    </div>
</template>

<script>
    import componentA from './router1/componentA'
    export default {
        components: {componentA},
        data() {
            return {
                router1: '第一栏',
                newVal: ''
            }
        }
    }
</script>

<style scoped>

</style>


